<template>
  <div>
    <tarjeta-credito
      :trans="translations"
      @change="guardar_info_tarjeta"
    ></tarjeta-credito>
    <div v-show="error" class="row">
      <div class="col-md-4"></div>
      <div class="col-md-4">
        <div class="alert alert-danger">
          Los datos de la tarjeta son incorrectos.
        </div>
      </div>
      <div class="col-md-4"></div>
    </div>
    <div v-show="pago_terminado" class="row">
      <div class="col-md-4"></div>
      <div class="col-md-4">
        <div v-show="resultado==1" class="alert alert-success">
          Pago realizado con exito
        </div>
        <div v-show="resultado==2" class="alert alert-danger">
          Eror en el pago
        </div>
      </div>
      <div class="col-md-4"></div>
    </div>
    <div v-show="loading" class="row">
      <div class="col-md-4"></div>
      <div class="col-md-4">
        Verificando tu pago, porfavor espera...
      </div>
      <div class="col-md-4"></div>
    </div>
    <div class="row" v-show="boton_visible">
      <div class="col-md-4"></div>
      <div class="col-md-4">
        <button type="button" class="btn btn-success" @click="validar_pago">Pagar</button>
      </div>
    </div>
  </div>
</template>

<script>
  import VCreditCard from "v-credit-card";
  import 'v-credit-card/dist/VCreditCard.css';
  import axios from 'axios';
  export default {
    data () {
      return {
        translations: {
          name:{
            label:'Nombre del titular',
            placeholder: 'Nombre completo'
          },
          card:{
            label:'Numero de tarjeta',
            placeholder:'Numero de tarjeta'
          },
          expiration:{
            label:'Vencimiento'
          },
          security:{
            label:'Codigo de seguridad',
            placeholder:'Codigo'
          }
        },
        card:{
          name:'',
          cardNumber:'',
          expiration:'',
          security:'',
          tipo:''
        },
        stripe_key:llave_publica,
        error:false,
        token_pago:'',
        url:url_pago,
        token:laravel_token,
        idservicio:servicio,
        loading:false,
        pago_terminado:false,
        resultado:0
      }
    },
    computed:{
      boton_visible:function () {
        var bandera = true;
        if (this.loading) {
          bandera = false;
        }
        if (this.pago_terminado) {
          bandera = false;
        }
        return bandera;
      }
    },
    methods:{
      guardar_info_tarjeta:function(values) {
        this.card.name=values.name;
        this.card.cardNumber=values.cardNumber;
        this.card.expiration=values.expiration;
        this.card.security=values.security;
      },
      validar_pago:function() {
        this.error=false;
        if (this.card.name=='') {
          this.error=true;
        }
        if (this.card.cardNumber=='') {
          this.error=true;
        }
        if (this.card.expiration=='') {
          this.error=true;
        }
        if (this.card.security=='') {
          this.error=true;
        }
        if (!this.error) {
          var tmp=this.card.expiration.split('/');
          this.loading=true;
          var self=this;
          Stripe.createToken({
            number:this.card.cardNumber,
            cvc:this.card.security,
            exp_month:tmp[0],
            exp_year:tmp[1]
          },function (status,response) {
            if (response.error) {
              alert('Error al pedir el token');
            }else{
              self.token_pago=response['id'];
              self.loading=true;
              console.log(response['id']);
              var datos = {
                idservicio:self.idservicio,
                _token:self.self.token,
                token_stripe:self.token_pago
              };
              axios.post(self.url,datos).then((result)=>{
                if (result.data.status=='Error') {
                  self.resultado=2;
                }else{
                  self.resultado=1;
                }
                self.loading=false;
                self.pago_terminado=true;
              }).catch(function(error) {
                self.loading=false;
                self.resultado=2;
                self.pago_terminado=true;
              })
            }
          });
        }
        
      }
    },
    components:{
      'tarjeta-credito':VCreditCard
    },
    created(){
      Stripe.setPublishableKey(this.stripe_key);
    }
  }
</script>

<style>

</style>



